@import "src/common/scss/mixins";

.container {
  border-radius: var(--radii);
  background-color: var(--colors-ui-base);
  box-shadow: var(--shadow);
  cursor: pointer;
  overflow: hidden;
  margin-bottom: 2rem;

  & img {
    display: block;
    width: 100%;
    height: 165px;
    object-fit: cover;
    object-position: center;
    box-shadow: var(--shadow);
    @media(max-width: 1023px) and (min-width: 360px) {
      height: 250px;
    }
    @media(max-width: 2206px) and (min-width: 1510px) {
      height: 200px;
    }
    @media(min-width: 22px) {
     // height: 200px;
    }

  }

  .body {
    padding: 2rem;

    & ul {
      list-style: none;
      margin: 0;
      padding: 1rem 0 0;
      font-size: var(--fs-sm);
      & li{
        line-height: 1.3rem;
      }
    }
  }
}